import { h } from 'preact';
import { Icon } from 'react-fa';
import FlexView from 'react-flexview';
import { connect } from 'preact-redux';
import { bindActionCreators } from 'redux';
import { toggleFilter } from '../../../actions/filter.action';
import { searchTools } from '../../../actions/search.action';
import { Search } from '../Search/Search';
import styles from './Filters.scss';

const FilterBox = ({ children }) => (
  <div className={styles.FilterBox}>{children}</div>
);

const FilterTitle = ({ children }) => (
  <h2 className={styles.FilterTitle}>{children}</h2>
);

const FilterInput = props => (
  <input className={styles.FilterInput} {...props} />
);

const FilterSectionTitle = ({ children }) => (
  <h3 className={styles.FilterSectionTitle}>{children}</h3>
);

const SearchContainer = ({ children }) => (
  <div className={styles.SearchContainer}>{children}</div>
);

const searchContainerStyle = {
  position: 'relative',
};

const searchIconStyle = {
  position: 'absolute',
  right: '0px',
  top: '8px',
  color: 'black',
};

const FilterTextStyle = ({ children }) => (
  <div className={styles.FilterTextStyle}>{children}</div>
);

const FiltersComponent = ({ filters, toggleFilter, searchTools }) => (
  <FilterBox>
    <FilterTitle>Filters</FilterTitle>
    <SearchContainer>
      <Search
        containerStyle={searchContainerStyle}
        iconStyle={searchIconStyle}
      />
    </SearchContainer>
    {[...new Set(filters.map(f => f.section))].map(section => {
      const fs = filters.filter(f => section === f.section).map((f, i) => (
        <FlexView column={false}>
          <input
            type="checkbox"
            value={f.key}
            checked={f.value}
            onChange={() => toggleFilter(f)}
          />
          <FilterTextStyle>{f.text}</FilterTextStyle>
        </FlexView>
      ));
      return [<FilterSectionTitle>{section}</FilterSectionTitle>].concat(fs);
    })}
  </FilterBox>
);

export const Filters = connect(
  state => ({
    filters: state.filters,
  }),
  dispatch =>
    bindActionCreators(
      {
        toggleFilter,
        searchTools,
      },
      dispatch
    )
)(FiltersComponent);



