import { h } from 'preact';
import FlexView from 'react-flexview';
import { connect } from 'preact-redux';
import { bindActionCreators } from 'redux';

import { Title } from '../../Shared/Title/Title';
import { ToolCard } from '../../Shared/Card/ToolCard/ToolCard';
import { Filters } from '../../Shared/Filters/Filters';
import { FilterTag } from '../../Shared/FilterTag/FilterTag';

const ViewAllComponent = ({ tools, filters }) => (
  <FlexView wrap={true}>
    <FlexView
      marginTop={15}
      marginRight={15}
      marginLeft={15}
      wrap={true}
      grow={1}
      basis={150}>
      <Filters />
    </FlexView>
    <FlexView
      column={true}
      grow={6}
      marginLeft={10}
      marginRight={10}
      basis={400}>
      <FilterTag />
      <Title title="View All Tools" />
      <FlexView column={false} wrap={true}>
        {tools.map((tool, index) => (
          <FlexView key={index} marginRight={15} marginBottom={15}>
            <ToolCard
              imageUrl="http://via.placeholder.com/190x80"
              summary={tool.description}
              title={tool.short}
              filters={tool.filters.map(tf =>
                filters
                  .filter(f => tf === f.key)
                  .map(f => f.text)
                  .reduce((x, y) => x.concat(y))
              )}
              toolhref={tool.anchor.tool}
              infohref={tool.anchor.info}
            />
          </FlexView>
        ))}
      </FlexView>
    </FlexView>
  </FlexView>
);

export const ViewAll = connect(({ tools, filters }) => ({
  filters,
  tools: tools.current,
}))(ViewAllComponent);



