import 'rxjs/add/operator/map';
import { TOGGLE_FILTER } from '../actions/filter.action';
import { setTools } from '../actions/tools.action';

export const filterEpic = (action$, store) =>
  action$
    .ofType(TOGGLE_FILTER)
    .map(() => {
      const checked = store.getState().filters.filter(f => f.value);
      const all = store.getState().tools.all;
      return {
        all,
        current:
          checked.length > 0
            ? checked
                .map(f => {
                  return store
                    .getState()
                    .tools.all.filter(t => t.filters.indexOf(f.key) !== -1);
                })
                .reduce((x, y) => x.concat(y))
            : all,
      };
    })
    .map(({ current, all }) => setTools(current, all));



