export const toggleFilterReducer = (state, { filter }) => ({
  ...state,
  filters: ((filters, filter) => {
    filters = filters.map(f => {
      if (f.key === filter.key) {
        f.value = !f.value;
      }
      return f;
    });
    return filters;
  })(state.filters, filter),
});



