import { h } from 'preact';
import { connect } from 'preact-redux';
import { bindActionCreators } from 'redux';
import styles from './FilterTag.scss';

const FilterLine = ({ children }) => (
  <div className={styles.FilterLine}>{children}</div>
);

const Tag = ({ children }) => <div className={styles.Tag}>{children}</div>;

const FilterTagComponent = ({ filters }) => (
  <FilterLine>
    {filters.length > 0 ? <span>Filters:</span> : null}
    {filters.map((name, index) => (
      <Tag key={index}>
        <span>{name}</span>
      </Tag>
    ))}
  </FilterLine>
);

export const FilterTag = connect(state => ({
  filters: state.filters
    .filter(filter => filter.value)
    .map(filter => filter.text),
}))(FilterTagComponent);



