import { h } from 'preact';
import FlexView from 'react-flexview';
import styles from './Landing.scss';
import { Search } from '../../Shared/Search/Search';
import { searchTools } from '../../../actions/search.action';
import { PrimaryButton } from '../../Shared/Button/PrimaryButton';
import { ToolCard } from '../../Shared/Card/ToolCard/ToolCard';
import Collection from '../../Shared/Card/Collection/Collection';
import { connect } from 'preact-redux';

const HeroSearch = ({ children }) => (
  <div className={styles.HeroSearch}>{children}</div>
);

const Heading1 = ({ children }) => (
  <h1 className={styles.Heading1}>{children}</h1>
);

const NewTool = ({ children }) => (
  <h2 className={styles.Heading2}>{children}</h2>
);

const ProviderFilter = ({ children }) => (
  <div className={styles.ProviderFilter}>{children}</div>
);

const PopularTools = ({ children }) => (
  <div className={styles.PopularTools}>{children}</div>
);

const PTContainer = ({ children }) => (
  <div className={styles.PTContainer}>{children}</div>
);

const Heading3 = ({ children }) => (
  <h3 className={styles.Heading3}>{children}</h3>
);

const Collections = ({ children }) => (
  <div className={styles.Collections}>{children}</div>
);

const ColContainer = ({ children }) => (
  <div className={styles.ColContainer}>{children}</div>
);

const searchContainerStyle = {
  position: 'relative',
  justifyContent: 'space-between',
  width: '100%',
};

const searchIconStyle = {
  position: 'absolute',
  top: '8px',
  right: '0px',
  color: 'black',
};

export const LandingComponents = ({ tools, filters }) => (
  <div>
    <HeroSearch>
      <Heading1>Welcome to the Analytics Hub</Heading1>
      <NewTool>Provider Lookup Tool</NewTool>
      <p>
        View a quick snapshot of any provider in the lorem ipsum dor sit amet.
      </p>
      <div className={styles.providerSearch}>
        <Search
          containerStyle={searchContainerStyle}
          iconStyle={searchIconStyle}
          placeholder={'Enter Provider ID or Tax ID'}
        />
      </div>
      <div className={styles.providerSearch}>
        <p>
          <input
            type="text"
            placeholder="Email to Send Results to"
            pattern="^[a-zA-Z0-9._%+-]+@oig\.hhs\.gov$"
            className={styles.Email}
          />
          <br className={styles.dateSearchBreak} />
        </p>
        <p>
          From
          <input type="date" className={styles.dateSearch} />
          <br className={styles.dateSearchBreak} />
          To
          <input type="date" className={styles.dateSearch} />
        </p>
      </div>
      <div className={styles.providerSearch}>
        <FlexView hAlignContent="center">
          <PrimaryButton text="GO" />
        </FlexView>
      </div>
    </HeroSearch>

    <div>
      <PopularTools>
        <PTContainer>
          <Heading3>Popular Tools and Resources</Heading3>
          <FlexView column={false} wrap={true} marginRight={15}>
            {tools.slice(0, 3).map((tool, index) => (
              <FlexView key={index} marginRight={15} marginBottom={15}>
                <ToolCard
                  imageUrl="http://via.placeholder.com/190x80"
                  summary={tool.description}
                  title={tool.short}
                  filters={tool.filters.map(tf =>
                    filters
                      .filter(f => tf === f.key)
                      .map(f => f.text)
                      .reduce((x, y) => x.concat(y))
                  )}
                  toolhref={tool.anchor.tool}
                  infohref={tool.anchor.info}
                />
              </FlexView>
            ))}
          </FlexView>
        </PTContainer>
      </PopularTools>
    </div>

    <div>
      <Collections>
        <ColContainer>
          <Heading3>Popular Tools and Resources</Heading3>
          <FlexView column={false} wrap={true} marginRight={15}>
            <FlexView marginRight={15} marginBottom={15} wrap={true}>
              {[1, 2, 3].map(n => (
                <FlexView marginRight={15} marginBottom={15}>
                  <Collection />
                </FlexView>
              ))}
            </FlexView>
          </FlexView>
        </ColContainer>
      </Collections>
    </div>
  </div>
);

export const Landing = connect(({ tools, filters }) => ({
  tools: tools.all,
  filters,
}))(LandingComponents);



