import { h } from 'preact';
import { connect } from 'preact-redux';
import { bindActionCreators } from 'redux';
import { Link, route } from 'preact-router';
import FlexView from 'react-flexview/lib';
import { Icon } from 'react-fa';
import { Search } from '../../Shared/Search/Search';
import { searchTools } from '../../../actions/search.action';
import styles from './Header.scss';
const OIG = require('../../../assets/images/oig_logo.png');
const NIH = require('../../../assets/images/nih-ribbon.png');
const HHS = require('../../../assets/images/nihribbon-hhs.png');

const Banner = ({ children }) => (
  <div className={styles.Banner}>{children}</div>
);

const Title = ({ children }) => <div className={styles.Title}>{children}</div>;

const TopBar = ({ children }) => (
  <div className={styles.TopBar}>{children}</div>
);

const NavBar = ({ children }) => (
  <div className={styles.NavBar}>{children}</div>
);

const TopIcon = ({ src }) => <img src={src} className={styles.TopIcon} />;

const Logo = ({ src }) => <img src={src} className={styles.Logo} />;

const TopWords = ({ children }) => (
  <div className={styles.TopWords}>{children}</div>
);

const NavPara = ({ children }) => <a className={styles.NavPara}>{children}</a>;

const SearchContainer = ({ children }) => (
  <div className={styles.SearchContainer}>{children}</div>
);

const FilterInput = props => (
  <input {...props} className={styles.FilterInput} />
);
const searchContainerStyle = {
  position: 'relative',
  justifyContent: 'space-between',
  marginRight: '15px',
};

const searchIconStyle = {
  position: 'absolute',
  right: '0px',
  top: '8px',
  color: 'black',
};

const HeaderComponent = ({ searchTools }) => (
  <Banner>
    <TopBar>
      <FlexView grow={1}>
        <TopIcon src={NIH} />
        <TopWords>U.S. Department of Health &amp; Human Services </TopWords>
        <TopIcon src={HHS} />
        <TopWords>National Institutes of Health </TopWords>
      </FlexView>
    </TopBar>
    <NavBar>
      <FlexView hAlignContent="right">
        <Search
          containerStyle={searchContainerStyle}
          iconStyle={searchIconStyle}
        />
      </FlexView>
      <FlexView vAlignContent="center" wrap={true}>
        <FlexView marginRight="2em" wrap={true}>
          <Link href="/" style={{ textDecoration: 'none', color: 'white' }}>
            <Logo src={OIG} />
          </Link>
        </FlexView>
        <FlexView>
          <FlexView marginRight="1em" marginTop="1em">
            <Link
              href="/tools"
              style={{ textDecoration: 'none', color: 'white' }}>
              <NavPara>View All Tools</NavPara>
            </Link>
          </FlexView>
          <FlexView marginRight="1em" marginTop="1em">
            <Link href="/" style={{ textDecoration: 'none', color: 'white' }}>
              <NavPara>Tool Collections</NavPara>
            </Link>
          </FlexView>
          <FlexView marginRight="1em" marginTop="1em">
            <Link href="/" style={{ textDecoration: 'none', color: 'white' }}>
              <NavPara>CDAC Custom Analysis</NavPara>
            </Link>
          </FlexView>
        </FlexView>
      </FlexView>
    </NavBar>
  </Banner>
);

export const Header = connect(
  state => state,
  dispatch =>
    bindActionCreators(
      {
        searchTools,
      },
      dispatch
    )
)(HeaderComponent);



