import FlexView from 'react-flexview/lib';
import styles from './PrimaryButton.scss';

const Button = ({ href, children }) => (
  <a href={href} className={styles.Button} target="_blank">
    {children}
  </a>
);

export const PrimaryButton = ({ text, href }) => (
  <FlexView>
    <Button href={href}>{text}</Button>
  </FlexView>
);



