import { h, Component } from 'preact';
import { connect } from 'preact-redux';
import { bindActionCreators } from 'redux';
import { Header } from '../../components/AppShell/Header/Header';
import { getDateQuarters } from '../../actions/date-quarters.action';
import { AppRouter } from '../../components/Routes/AppRouter/AppRouter';

class AppShellComponent extends Component {
  constructor(props) {
    super(props);
  }
  componentWillMount() {
    this.props.getDateQuarters();
  }
  render() {
    return (
      <div style="text-align: center;">
        <Header />
        <AppRouter />
      </div>
    );
  }
}

export const AppShell = connect(
  state => state,
  dispatch => bindActionCreators({ getDateQuarters }, dispatch)
)(AppShellComponent);



