import { h, Component } from 'preact';
import FlexView from 'react-flexview';

import { PrimaryButton } from '../../Button/PrimaryButton';
import styles from './ToolCard.scss';

const Arrow = require('../../../../assets/images/arrow.png');
const Lock = require('../../../../assets/images/lock.png');

const ToolCardFrame = ({ children }) => (
  <div className={styles.ToolCardFrame}>{children}</div>
);

const CardTitle = ({ children }) => (
  <h4 className={styles.CardTitle}>{children}</h4>
);

const CardImage = ({ src, alt }) => (
  <img src={src} alt={alt} className={styles.CardImage} />
);

const CardDescription = ({ children }) => (
  <p className={styles.CardDescription}>{children}</p>
);

const AccordionTitle = ({ children }) => (
  <h5 className={styles.AccordionTitle}>{children}</h5>
);

const AccordionDescription = ({ children, className }) => (
  <p className={className}>{children}</p>
);

const AccordionLock = ({ src }) => (
  <img src={src} className={styles.AccordionLock} />
);

const AccordionText = ({ children }) => (
  <span className={styles.AccordionText}>{children}</span>
);

const ArrowIcon = ({ src, className }) => (
  <img src={src} className={className} />
);

const AccordionTags = props => (
  <div className={styles.AccordionTags} {...props}>
    {props.children}
  </div>
);

const Accordion = ({ children }) => (
  <div className={styles.Accordion}>{children}</div>
);

class AccordionElement extends Component {
  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(e) {
    e.preventDefault();
    this.setState({ active: !this.state.active });
  }

  render() {
    return (
      <div onClick={this.handleClick}>
        <Accordion isActive={this.state.active}>
          SHOW MORE &nbsp;
          <ArrowIcon
            src={Arrow}
            className={
              this.state.active ? styles.ArrowIcon : styles.ArrowIconRotate
            }
          />
        </Accordion>
        <AccordionDescription
          className={
            this.state.active ? styles.ShowAccordion : styles.HideAccordion
          }>
          <FlexView column={false} vAlignContent="top">
            <AccordionLock src={Lock} />
            <AccordionText>CMS ID Required for Access</AccordionText>
          </FlexView>
          <FlexView column={false} vAlignContent="top" wrap>
            {this.props.filters ? (
              <AccordionText>Collections:</AccordionText>
            ) : (
              <span />
            )}
            {this.props.filters ? (
              this.props.filters.map((filter, index) => (
                <AccordionTags key={index}>{filter}</AccordionTags>
              ))
            ) : (
              <span />
            )}
          </FlexView>
        </AccordionDescription>
      </div>
    );
  }
}

export const ToolCard = ({
  imageUrl,
  summary,
  title,
  filters,
  toolhref,
  infohref,
}) => (
  <ToolCardFrame>
    <CardImage src={imageUrl} alt={title + '-image'} />
    <CardTitle>{title}</CardTitle>
    <CardDescription>{summary}</CardDescription>
    <AccordionElement filters={filters} />

    <FlexView column={false}>
      <FlexView marginRight={15}>
        <PrimaryButton text="Go To Tool" href={toolhref} />
      </FlexView>
      <FlexView marginRight={15}>
        <PrimaryButton text="See Resources" href={infohref} />
      </FlexView>
    </FlexView>
  </ToolCardFrame>
);



