import { h, Component } from 'preact';
import styles from './Collection.scss';
import FlexView from 'react-flexview';

const Plus = require('../../../../assets/images/plus.png');

const FlipContainer = ({ children }) => (
  <div className={styles.FlipContainer}>{children}</div>
);

const Flipper = ({ children, className }) => (
  <div className={className}>{children}</div>
);
const Front = ({ children, className }) => (
  <div className={className}>{children}</div>
);
const FrontText = ({ children }) => (
  <h3 className={styles.FrontText}>{children}</h3>
);
const FrontContainer = ({ children }) => (
  <div className={styles.FrontContainer}>{children}</div>
);
const BackContainer = ({ children }) => (
  <div className={styles.BackContainer}>{children}</div>
);

const BackText = ({ children }) => (
  <div className={styles.BackText}>{children}</div>
);

const PlusIcon = ({ src }) => <img src={src} className={styles.PlusIcon} />;

const Divider = () => <hr className={styles.Divider} />;

const Back = ({ children, className }) => (
  <div className={className}>{children}</div>
);

const ButtonLine = ({ children }) => (
  <div className={styles.ButtonLine}>{children}</div>
);

const BackButton = ({ children }) => (
  <button className={styles.BackButton}>{children}</button>
);

export default class Collection extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isFlipped: false,
    };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(e) {
    this.setState({ isFlipped: !this.state.isFlipped });
  }

  render() {
    return (
      <FlipContainer onClick={this.handleClick}>
        <Flipper
          className={
            this.state.isFlipped ? styles.FlipperRotate : styles.Flipper
          }>
          <Front className={styles.Front}>
            <FrontContainer>
              <FrontText>Get a Case</FrontText>
              <Divider />
              <PlusIcon src={Plus} />
            </FrontContainer>
          </Front>
          <Back className={styles.Back}>
            <BackContainer>
              <BackText>
                Lorem ipsum dolor sit amet, tempor consectetur adipiscing elit,
                sed do eiusmod tempor inci sed do codtur ipsum dolor sit amet,
                tempor consectetur adipiscing elit, sed do eiusmod tempor inci
                sed do codtur do codtur ipsum dolor sit amet, tempor consectetur
              </BackText>
              <BackButton>
                Learn More
                <ButtonLine />
              </BackButton>
            </BackContainer>
          </Back>
        </Flipper>
      </FlipContainer>
    );
  }
}



